* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

body {
    font-family: "Poppins", sans-serif;
    background: #1d1d1d;
    text-align: center;
}

.app-container {
    width: 100%;
    height: 100%;
}

h1 {
    color: #fafafa;
    font-weight: 600;
    margin: 20px;
}

.header {
    width: 100%;
    height: 300px;
}

input {
    text-align: center;
    outline: none;
    border: none;
    width: 300px;
    padding: 10px;
    font-size: 1rem;
    border-radius: 7px;
    background: #fafafa;
    box-shadow: 1px 1px 15px rgba(255, 255, 255, 0.75);
}

.cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.meal-card {
    flex: 1 0 20%;
    min-width: 180px;
    max-width: 400px;
    border: 2px solid #fafafa;
    margin: 5px;
    padding: 10px;
    border-radius: 10px;
}

img {
    width: 90%;
    border-radius: 10px;
}

p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
    color: #c9c9c9;
}